<template>
  <el-container>
    <el-header>
      Mino管理面板
      <el-button size="mini" type="danger" @click="exit"> 关闭程序 </el-button>
    </el-header>
    <el-main>
      <el-tabs v-model="activeName">
        <el-tab-pane label="配置管理" name="setting">
          <setting />
        </el-tab-pane>
        <el-tab-pane label="实时日志" name="log">
          <log />
        </el-tab-pane>
      </el-tabs>
    </el-main>
  </el-container>
</template>

<script>
import Setting from "../components/Setting";
import Log from "../components/Log";
import service from "@/js/service";

export default {
  name: "Main",
  components: {
    Setting,
    Log,
  },
  data() {
    return {
      activeName: "setting",
    };
  },
  methods: {
    exit() {
      console.log("退出程序");
      service
        .exitProgram()
        .then(() => {
          this.$notify({
            type: "error",
            message: "关闭失败",
          });
        })
        .catch(() => {
          this.$notify({
            type: "success",
            message: "关闭成功",
          });
        });
    },
  },
};
</script>

